import React from 'react';
import Header from '../Header/Header';
import Navbar from '../Navbar/Navbar';
import apple from '../../assets/images/products/product-img-15.jpg';
import Pineapple from '../../assets/images/products/product-img-16.jpg';
import kiwi from '../../assets/images/products/product-img-17.jpg';
import banana from '../../assets/images/products/product-img-18.jpg';
import Beatroot from '../../assets/images/products/product-img-19.jpg';
import Freshapple from '../../assets/images/products/product-img-15.jpg';
import GoldenPineapple from '../../assets/images/products/product-img-16.jpg';

const productData = [
  {
    id: '1',
    image: banana,
    name: 'Organic Banana',
    price: '$35.00',
    amount: '$.98 / lb',
    status: 'In Stock',
    statusClass: 'bg-success',
    buttonText: 'Add to Cart',
    buttonClass: 'btn-primary'
  },
  {
    id: '2',
    image: kiwi,
    name: 'Fresh Kiwi',
    price: '$20.97',
    amount: '4 no',
    status: 'Out of Stock',
    statusClass: 'bg-danger',
    buttonText: 'Contact us',
    buttonClass: 'btn-dark'
  },
  {
    id: '3',
    image: GoldenPineapple,
    name: 'Golden Pineapple',
    price: '$35.00',
    amount: '2 no',
    status: 'In Stock',
    statusClass: 'bg-success',
    buttonText: 'Add to Cart',
    buttonClass: 'btn-primary'
  },
  {
    id: '4',
    image: Beatroot,
    name: 'BeatRoot',
    price: '$29.00',
    amount: '1 kg',
    status: 'In Stock',
    statusClass: 'bg-success',
    buttonText: 'Add to Cart',
    buttonClass: 'btn-primary'
  },
  {
    id: '5',
    image: apple,
    name: 'Fresh Apple',
    price: '$70.00',
    amount: '2 kg',
    status: 'In Stock',
    statusClass: 'bg-success',
    buttonText: 'Add to Cart',
    buttonClass: 'btn-primary'
  },
];

function Wishlist() {
  return (
    <>
      <Header />
      <Navbar />
      <section className="my-lg-14 my-8">
        <div className="container">
          <div className="row align-items-center mb-6">
            <div className="col-12">
              <h1 className="mb-1">My Wishlist</h1>
              <p>There are {productData.length} products in this wishlist.</p>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <div className="table-responsive">
                <table className="table text-nowrap">
                  <thead className="table-light">
                    <tr>
                      <th>
                        <div className="form-check">
                          <input className="form-check-input" type="checkbox" value="" id="checkAll" />
                          <label className="form-check-label" htmlFor="checkAll"></label>
                        </div>
                      </th>
                      <th></th>
                      <th>Product</th>
                      <th>Amount</th>
                      <th>Status</th>
                      <th>Actions</th>
                      <th>Remove</th>
                    </tr>
                  </thead>
                  <tbody>
                    {productData.map(product => (
                      <tr key={product.id}>
                        <td className="align-middle">
                          <div className="form-check">
                            <input className="form-check-input" type="checkbox" value="" id={`checkbox${product.id}`} />
                            <label className="form-check-label" htmlFor={`checkbox${product.id}`}></label>
                          </div>
                        </td>
                        <td className="align-middle">
                          <a href="#"><img src={product.image} className="icon-shape icon-xxl" alt={product.name} /></a>
                        </td>
                        <td className="align-middle">
                          <div>
                            <h5 className="fs-6 mb-0"><a href="#" className="text-inherit">{product.name}</a></h5>
                            <small>{product.amount}</small>
                          </div>
                        </td>
                        <td className="align-middle">{product.price}</td>
                        <td className="align-middle"><span className={`badge ${product.statusClass}`}>{product.status}</span></td>
                        <td className="align-middle">
                          <div className={`btn ${product.buttonClass} btn-sm`}>{product.buttonText}</div>
                        </td>
                        <td className="align-middle">
                          <a href="#" className="text-muted" data-bs-toggle="tooltip" data-bs-placement="top" aria-label="Delete">
                            <i className="feather-icon icon-trash-2"></i>
                          </a>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
              
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default Wishlist;
