import React from 'react';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import '../../assets/libs/tiny-slider/dist/tiny-slider.css';
import '../../assets/libs/slick-carousel/slick/slick.css';
import '../../assets/libs/slick-carousel/slick/slick-theme.css';
import '../../assets/libs/simplebar/dist/simplebar.min.css';
import '../../assets/css/theme.min.css';
import "../../../src/Screens/AdminNavbar/AdminNavbar.css";
import Logo from "../../../src/assets/images/logo/freshcart-logo.svg";
import { BsCart, BsListUl, BsBag, BsHouseGearFill, BsPeople, BsStar, BsArrow90DegDown } from "react-icons/bs";

function AdminNavbar({ isSidebarVisible }) {
  return (
    <nav className={`navbar-vertical-nav d-none d-xl-block ${isSidebarVisible ? '' : 'd-none'}`}>
      <div className="navbar-vertical">
        <div className="px-4 py-5">
          <a href="../index.html" className="navbar-brand">
            <img src={Logo} alt="FreshCart Logo" />
          </a>
        </div>
        <div className="navbar-vertical-content flex-grow-1" data-simplebar="">
          <ul className="navbar-nav flex-column" id="sideNavbar">
            <li className="nav-item">
              <Link className="nav-link" to="/adminDashboard">
                <div className="d-flex align-items-center">
                  <span className="nav-link-icon"><i className="bi bi-house"></i></span>
                  <span className="nav-link-text">Dashboard</span>
                </div>
              </Link>
            </li>
            <li className="nav-item mt-6 mb-3">
              <span className="nav-label">Store Managements</span>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/products">
                <div className="d-flex align-items-center">
                  <span className="nav-link-icon"><i className="bi bi-cart"></i><BsCart /></span>
                  <span className="nav-link-text">priceband</span>
                </div>
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/items">
                <div className="d-flex align-items-center">
                  <span className="nav-link-icon"><i className="bi bi-list-task"></i><BsListUl /></span>
                  <span className="nav-link-text">Items</span>
                </div>
              </Link>
            </li>
            <li className="nav-item">
              <a className="nav-link collapsed" href="#" data-bs-toggle="collapse" data-bs-target="#navCategoriesOrders" aria-expanded="false" aria-controls="navCategoriesOrders">
                <div className="d-flex align-items-center">
                  <span className="nav-link-icon"><i className="bi bi-bag"></i><BsBag /></span>
                  <span className="nav-link-text">All Category</span>
                </div>
              </a>
              <div id="navCategoriesOrders" className="collapse" data-bs-parent="#sideNavbar">
                <ul className="nav flex-column">
                <Link className="nav-link" to="/category">
                  <li className="nav-item">
                    <a className="nav-link" href="">Category</a>
                    </li>
                </Link>
                <Link className="nav-link" to="/subcategory">
                  <li className="nav-item"><a className="nav-link" href="">Subcategory</a></li>
                </Link>
                <Link className="nav-link" to="/sub-subcategory">
                  <li className="nav-item"><a className="nav-link" href="">SubSubCategory</a></li>
                </Link>
                </ul>
              </div>
            </li>
            <li className="nav-item"><a className="nav-link" href="vendor-grid.html"><div className="d-flex align-items-center"><span className="nav-link-icon"><i className="bi bi-shop"></i><BsHouseGearFill /></span><span className="nav-link-text">Sellers / Vendors</span></div></a></li>
            <li className="nav-item"><a className="nav-link" href="customers.html"><div className="d-flex align-items-center"><span className="nav-link-icon"><i className="bi bi-people"></i><BsPeople /></span><span className="nav-link-text">Customers</span></div></a></li>
            <li className="nav-item"><a className="nav-link" href="reviews.html"><div className="d-flex align-items-center"><span className="nav-link-icon"><i className="bi bi-star"></i><BsStar /></span><span className="nav-link-text">Reviews</span></div></a></li>
            <li className="nav-item">
              <a className="nav-link collapsed" href="#" data-bs-toggle="collapse" data-bs-target="#navMenuLevelFirst" aria-expanded="false" aria-controls="navMenuLevelFirst">
                <span className="nav-link-icon"><i className="bi bi-arrow-90deg-down"></i><BsArrow90DegDown /></span>
                <span className="nav-link-text">Menu Level</span>
              </a>
              <div id="navMenuLevelFirst" className="collapse" data-bs-parent="#sideNavbar">
                <ul className="nav flex-column">
                  <li className="nav-item">
                    <a className="nav-link" href="#" data-bs-toggle="collapse" data-bs-target="#navMenuLevelSecond1" aria-expanded="false" aria-controls="navMenuLevelSecond1">
                      Two Level
                    </a>
                    <div id="navMenuLevelSecond1" className="collapse" data-bs-parent="#navMenuLevel">
                      <ul className="nav flex-column">
                        <li className="nav-item"><a className="nav-link" href="#">NavItem 1</a></li>
                        <li className="nav-item"><a className="nav-link" href="#">NavItem 2</a></li>
                      </ul>
                    </div>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link collapsed" href="#" data-bs-toggle="collapse" data-bs-target="#navMenuLevelThreeOne1" aria-expanded="false" aria-controls="navMenuLevelThreeOne1">
                      Three Level
                    </a>
                    <div id="navMenuLevelThreeOne1" className="collapse" data-bs-parent="#navMenuLevel">
                      <ul className="nav flex-column">
                        <li className="nav-item">
                          <a className="nav-link collapsed" href="#" data-bs-toggle="collapse" data-bs-target="#navMenuLevelThreeTwo" aria-expanded="false" aria-controls="navMenuLevelThreeTwo">
                            NavItem 1
                          </a>
                          <div id="navMenuLevelThreeTwo" className="collapse" data-bs-parent="#navMenuLevelThree">
                            <ul className="nav flex-column">
                              <li className="nav-item"><a className="nav-link" href="#">NavChild Item 1</a></li>
                            </ul>
                          </div>
                        </li>
                        <li className="nav-item"><a className="nav-link" href="#">Nav Item 2</a></li>
                      </ul>
                    </div>
                  </li>
                </ul>
              </div>
            </li>
            <li className="nav-item mt-6 mb-3"><span className="nav-label">Site Settings</span><span className="badge bg-light-info text-dark-info">Coming Soon</span></li>
            <li className="nav-item"><a className="nav-link disabled" href="#"><div className="d-flex align-items-center"><span className="nav-link-icon"><i className="bi bi-newspaper"></i></span><span className="nav-link-text">Blog</span></div></a></li>
            <li className="nav-item"><a className="nav-link disabled" href="#"><div className="d-flex align-items-center"><span className="nav-link-icon"><i className="bi bi-images"></i></span><span className="nav-link-text">Media</span></div></a></li>
            <li className="nav-item"><a className="nav-link disabled" href="#"><div className="d-flex align-items-center"><span className="nav-link-icon"><i className="bi bi-gear"></i></span><span className="nav-link-text">Store Settings</span></div></a></li>
            <li className="nav-item mt-6 mb-3"><span className="nav-label">Support</span><span className="badge bg-light-info text-dark-info">Coming Soon</span></li>
            <li className="nav-item"><a className="nav-link disabled" href="#"><div className="d-flex align-items-center"><span className="nav-link-icon"><i className="bi bi-headphones"></i></span><span className="nav-link-text">Support Ticket</span></div></a></li>
            <li className="nav-item"><a className="nav-link disabled" href="#"><div className="d-flex align-items-center"><span className="nav-link-icon"><i className="bi bi-lock"></i></span><span className="nav-link-text">LogOut</span></div></a></li>
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default AdminNavbar;
