import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { useNavigate } from 'react-router-dom';
import useApi from '../../src/service/useApi';
import { toast } from 'react-toastify';

function AdminProductModal(props) {
    const { apiAction } = useApi();
    const [type, setType] = useState('');
    const [loading, setLoading] = useState(false); // Step 1: Introduce loading state
    const adminLogin = JSON.parse(localStorage.getItem('adminLogin'));

    const handleUploadData = async (e) => {
        e.preventDefault();
        setLoading(true); // Step 2: Set loading to true before API call
        const requestBody = {
            type: type,
        };
        try {
            const response = await apiAction({
                url: "/admin/item/priceband/create",
                method: "POST",
                body: requestBody,
                headers: {
                    'Authorization': `Bearer ${adminLogin?.token}`
                }
            });
            if (response.status) {
                toast.success(response?.message);
                props.onHide();
                props.onSuccess();
            } else {
                toast.error(response?.message && response.data.type[0]);
            }
        } catch (error) {
            console.log(error, "------error");
        } finally {
            setLoading(false); // Step 2: Set loading back to false after API call
        }
    };

    return (
        <Modal
            {...props}
            size="sm"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Body>
                <div className="modal-content p-1">
                    <div className="modal-header border-0">
                        <h5 className="modal-title fs-3 fw-bold" id="userModalLabel">Upload Data</h5>
                        <Modal.Header closeButton />
                    </div>
                    <div className="modal-body">
                        <form className="needs-validation" noValidate onSubmit={handleUploadData}>
                            <div className="mb-3">
                                <label htmlFor="type" className="form-label">Type</label>
                                <input
                                    value={type}
                                    className="form-control"
                                    id="type"
                                    placeholder="Type"
                                    onChange={(e) => setType(e.target.value)}
                                    required
                                />
                                <div className="invalid-feedback">Please enter Item Type.</div>
                            </div>
                            <div className="d-flex mb-3">
                                <Button
                                    type="button"
                                    className="btn btn-secondary w-100"
                                    onClick={props.onHide}
                                    disabled={loading} // Step 3: Disable button when loading
                                >
                                    Close
                                </Button>
                                <Button
                                    type="submit"
                                    className="btn btn-primary w-100"
                                    style={{ marginLeft: 7 }}
                                    disabled={loading} // Step 3: Disable button when loading
                                >
                                    {loading ? 'Saving...' : 'Save'} {/* Show "Saving..." when loading */}
                                </Button>
                            </div>
                        </form>
                    </div>
                </div>
            </Modal.Body>
        </Modal>
    );
}

export default AdminProductModal;
