import React, { useEffect, useState } from 'react';
import AdminProfile from '../AdminProfile/AdminProfile';
import AdminNavbar from '../AdminNavbar/AdminNavbar';
import { BsThreeDotsVertical } from "react-icons/bs";
import { toast } from 'react-toastify';
import useApi from '../../service/useApi';
import AdminItemsModal from '../../commponets/AdminItemsModal';
import { BsTrash, BsPencilSquare, BsEye } from "react-icons/bs";
import AdminItemsEditModal from '../../commponets/AdminItemsEditModal';
import { format } from 'date-fns';
import AdminViewItem from '../../commponets/AdminViewItem';

function AdminItems() {
  const [modalShow, setModalShow] = useState(false);
  const [editmodalShow, setEditModalShow] = useState(false);
  const [viewItem, setViewItem] = useState(false);
  const [aItemList, setAllItemList] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const { apiAction } = useApi();
  const adminLogin = JSON.parse(localStorage.getItem('adminLogin'));
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredPricebandList, setFilteredPricebandList] = useState([]);
  const [editItemId, setEditItemId] = useState(null);
  const [editItemData, setEditItemData] = useState(null);
  const [ViewAllItem, setViewAllItem] = useState(null);
  const [selectedItemId, setSelectedItemId] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchPricebandList(currentPage);
  }, [currentPage]);

  useEffect(() => {
    filterPricebandList();
  }, [searchTerm, aItemList]);

  // item_list
  const fetchPricebandList = async (page) => {
    setLoading(true);
    try {
      const response = await apiAction({
        url: `/admin/item/list?page=${page}`,
        method: "GET",
        headers: {
          'Authorization': `Bearer ${adminLogin?.token}`
        }
      });
      if (response.status) {
        const pricebandData = response?.data?.data;
        setAllItemList(pricebandData);
        setFilteredPricebandList(pricebandData);
        setTotalPages(response?.data?.last_page);
      } else {
        console.log(response, "------response");
        toast.error(response?.message);
      }
    } catch (error) {
      toast.error("Failed to fetch data.");
    } finally {
      setLoading(false);
    }
  };

  const filterPricebandList = () => {
    if (searchTerm.trim() === '') {
      setFilteredPricebandList(aItemList);
    } else {
      const filteredList = aItemList.filter(item =>
        item.type?.toLowerCase().includes(searchTerm.toLowerCase())
      );
      setFilteredPricebandList(filteredList);
    }
  };

  const handleSearchInputChange = (e) => {
    setSearchTerm(e.target.value);
  };

  const handlePageChange = (newPage) => {
    if (newPage >= 1 && newPage <= totalPages) {
      setCurrentPage(newPage);
    }
  };

  const handleModalSuccess = () => {
    fetchPricebandList();
  };

  // item_delete
  const handleDeleteItem = async (itemId) => {
    try {
      const response = await apiAction({
        url: `/admin/item/delete/${itemId}`,
        method: "DELETE",
        headers: {
          'Authorization': `Bearer ${adminLogin?.token}`
        }
      });
      if (response.status) {
        toast.success(response?.message);
        fetchPricebandList();
      } else {
        console.log(response,"------");
        toast.error(response?.message);
      }
    } catch (error) {
      console.error("Error deleting item:", error);
      toast.error("Failed to delete item.");
    }
  };

  const handleEditItemClick = (itemId) => {
    setEditItemData(itemId);
    setEditItemId(itemId);
    setEditModalShow(true);
  };

  const handleViewDetails = (itemId) => {
    setSelectedItemId(itemId);
    setViewItem(true);
  };

  return (
    <>
      <div className="main-wrapper">
        <AdminProfile />
        <AdminNavbar />
        <AdminItemsModal show={modalShow} onHide={() => setModalShow(false)} onSuccess={handleModalSuccess} />
        <AdminItemsEditModal show={editmodalShow} onHide={() => setEditModalShow(false)} itemId={editItemId} onSuccess={handleModalSuccess} itemData={editItemData} />
        <AdminViewItem show={viewItem} onHide={() => setViewItem(false)} itemId={selectedItemId} />
        <main className="main-content-wrapper">
          <div className="container">
            <div className="row mb-8">
              <div className="col-md-12">
                <div className="d-flex flex-column flex-md-row justify-content-between align-items-md-center gap-4">
                  <div>
                    <h2>Items</h2>
                    <nav aria-label="breadcrumb">
                      <ol className="breadcrumb mb-0">
                        <li className="breadcrumb-item">
                          <a href="categories.html#" className="text-inherit">
                            Dashboard
                          </a>
                        </li>
                        <li className="breadcrumb-item active" aria-current="page">
                          Items
                        </li>
                      </ol>
                    </nav>
                  </div>
                  <div>
                    <a href="#" role="button" onClick={() => setModalShow(true)} className="btn btn-primary">
                      Add New Item
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-xl-12 col-12 mb-5">
                <div className="card h-100 card-lg">
                  <div className="px-6 py-6">
                    <div className="row justify-content-between">
                      <div className="col-lg-4 col-md-6 col-12 mb-2 mb-md-0">
                        <form className="d-flex" role="search">
                          <input
                            className="form-control"
                            type="search"
                            placeholder="Search Item"
                            aria-label="Search"
                            value={searchTerm}
                            onChange={handleSearchInputChange}
                          />
                        </form>
                      </div>
                      <div className="col-xl-2 col-md-4 col-12">
                        <select className="form-select">
                          <option selected="">Status</option>
                          <option value="Published">Published</option>
                          <option value="Unpublished">Unpublished</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div className="card-body p-0">
                    <div className="table-responsive">
                      {loading ? (
                        <div className="text-center py-5">
                          <div className="spinner-border" role="status">
                            <span className="visually-hidden">Loading...</span>
                          </div>
                        </div>
                      ) : (
                        <table className="table table-centered table-hover mb-0 text-nowrap table-borderless table-with-checkbox">
                          <thead className="bg-light">
                            <tr>
                              <th>Name</th>
                              <th>Stock</th>
                              <th>Price</th>
                              <th>Discount</th>
                              <th>Description</th>
                              <th>Created at</th>
                              <th>Updated at</th>
                              <th />
                            </tr>
                          </thead>
                          <tbody>
                            {aItemList.map((item) => (
                              <tr key={item.id}>
                                <td>
                                  <a href="categories.html#" className="text-reset">
                                    {item.name}
                                  </a>
                                </td>
                                <td>{item.stock}</td>
                                <td>
                                  <span className="badge bg-light-primary text-dark-primary">
                                    {item.price}
                                  </span>
                                </td>
                                <td>{item.discount}</td>
                                <td>{item.description}</td>
                                <td>{format(new Date(item.created_at), "dd MMMM yyyy hh:mm:ss a")}</td>
                                <td>{format(new Date(item.updated_at), "dd MMMM yyyy hh:mm:ss a")}</td>
                                <td>
                                  <div className="dropdown">
                                    <a
                                      href="categories.html#"
                                      className="text-reset"
                                      data-bs-toggle="dropdown"
                                      aria-expanded="false"
                                    >
                                      <BsThreeDotsVertical />
                                    </a>
                                    <ul className="dropdown-menu">
                                      <li>
                                        <button
                                          className="btn-link dropdown-item"
                                          onClick={() => handleDeleteItem(item.id)}
                                        >
                                          <BsTrash />
                                          Delete
                                        </button>
                                      </li>
                                      <li>
                                        <button
                                          className="btn-link dropdown-item"
                                          onClick={() => handleEditItemClick(item)}
                                        >
                                          <BsPencilSquare />
                                          Edit
                                        </button>
                                      </li>
                                      <li>
                                        <button
                                          className="btn-link dropdown-item"
                                          onClick={() => handleViewDetails(item.id)}
                                        >
                                          <BsEye />
                                          View Order in Details
                                        </button>
                                      </li>
                                    </ul>
                                  </div>
                                </td>
                              </tr>
                            ))}
                          </tbody>
                        </table>
                      )}
                    </div>
                  </div>
                  <div className="border-top d-flex justify-content-between align-items-md-center px-6 py-6 flex-md-row flex-column gap-4">
                    <span>Showing {currentPage} to {totalPages} of {aItemList.length} entries</span>
                    <nav>
                      <ul className="pagination mb-0">
                        <li className={`page-item ${currentPage === 1 ? 'disabled' : ''}`}>
                          <button className="page-link" onClick={() => handlePageChange(currentPage - 1)}>
                            Previous
                          </button>
                        </li>
                        {[...Array(totalPages)].map((_, index) => (
                          <li key={index} className={`page-item ${currentPage === index + 1 ? 'active' : ''}`}>
                            <button className="page-link" onClick={() => handlePageChange(index + 1)}>
                              {index + 1}
                            </button>
                          </li>
                        ))}
                        <li className={`page-item ${currentPage === totalPages ? 'disabled' : ''}`}>
                          <button className="page-link" onClick={() => handlePageChange(currentPage + 1)}>
                            Next
                          </button>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </main>
      </div>
    </>
  );
}

export default AdminItems;
