import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import second from "../../assets/images/logo/freshcart-logo.svg";
import '../../assets/libs/tiny-slider/dist/tiny-slider.css';
import '../../assets/libs/slick-carousel/slick/slick.css';
import '../../assets/libs/slick-carousel/slick/slick-theme.css';
import '../../assets/libs/simplebar/dist/simplebar.min.css';
import "../../assets/css/theme.min.css";
import "../../../src/App.css";
import { Link } from 'react-router-dom';
import Button from 'react-bootstrap/Button'; 
import Modal from 'react-bootstrap/Modal';
import RegisterModal from '../../commponets/RegisterModal';
import LoginModal from '../../commponets/LoginModal';

function Header() {
    const [modalShow, setModalShow] = useState(false);
    const [modalShowCart, setModalShowCart] = useState(false);

    return (
        <>
            <LoginModal show={modalShow} onHide={() => setModalShow(false)} />
          
            <header className="py-lg-5 py-4 border-bottom border-bottom-lg-0">
                <div className="container">
                    <div className="row align-items-center gx-3">
                        <div className="col-xl-7 col-lg-8 col-md-6">
                            <div className="d-flex align-items-center">
                                <a className="navbar-brand d-none d-lg-block" href="../index.html">
                                    <img src={second} alt="eCommerce HTML Template" />
                                </a>
                                <div className="w-100 ms-4 d-none d-lg-block">
                                    <form>
                                        <div className="input-group">
                                            <input className="form-control rounded" type="search" placeholder="Search for products" />
                                            <span className="input-group-append">
                                                <button className="btn bg-white border border-start-0 ms-n10 rounded-0 rounded-end" type="button">
                                                    <svg
                                                        xmlns="http://www.w3.org/2000/svg"
                                                        width="16"
                                                        height="16"
                                                        viewBox="0 0 24 24"
                                                        fill="none"
                                                        stroke="currentColor"
                                                        strokeWidth="2"
                                                        strokeLinecap="round"
                                                        strokeLinejoin="round"
                                                        className="feather feather-search"
                                                    >
                                                        <circle cx="11" cy="11" r="8"></circle>
                                                        <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
                                                    </svg>
                                                </button>
                                            </span>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>

                        <div className="col-xl-3 col-lg-4 col-md-6 col-12 text-end">
                            <div className="list-inline">
                                <div className="list-inline-item me-3">
                                    <Link to={'/wishlist'} className="text-muted position-relative">
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            width="20"
                                            height="20"
                                            viewBox="0 0 24 24"
                                            fill="none"
                                            stroke="currentColor"
                                            strokeWidth="2"
                                            strokeLinecap="round"
                                            strokeLinejoin="round"
                                            className="feather feather-heart"
                                        >
                                            <path d="M20.84 4.61a5.5 5.5 0 0 0-7.78 0L12 5.67l-1.06-1.06a5.5 5.5 0 0 0-7.78 7.78l1.06 1.06L12 21.23l7.78-7.78 1.06-1.06a5.5 5.5 0 0 0 0-7.78z"></path>
                                        </svg>
                                        <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-success">
                                            5
                                            <span className="visually-hidden">unread messages</span>
                                        </span>
                                    </Link>
                                </div>

                                <div className="list-inline-item me-3">
                                    <a className="text-muted position-relative" href="#" role="button" onClick={() => setModalShow(true)}>
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            width="20"
                                            height="20"
                                            viewBox="0 0 24 24"
                                            fill="none"
                                            stroke="currentColor"
                                            strokeWidth="2"
                                            strokeLinecap="round"
                                            strokeLinejoin="round"
                                            className="feather feather-user"
                                        >
                                            <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"></path>
                                            <circle cx="12" cy="7" r="4"></circle>
                                        </svg>
                                    </a>
                                </div>

                                <div className="list-inline-item me-3">
                                    <Link to={'/cart'} className="text-muted position-relative">
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            width="20"
                                            height="20"
                                            viewBox="0 0 24 24"
                                            fill="none"
                                            stroke="currentColor"
                                            strokeWidth="2"
                                            strokeLinecap="round"
                                            strokeLinejoin="round"
                                            className="feather feather-shopping-bag"
                                        >
                                            <path d="M6 2L3 6v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2V6l-3-4z"></path>
                                            <line x1="3" y1="6" x2="21" y2="6"></line>
                                            <path d="M16 10a4 4 0 0 1-8 0"></path>
                                        </svg>
                                        <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-success">
                                            1
                                            <span className="visually-hidden">unread messages</span>
                                        </span>
                                    </Link>
                                </div>

                                <div className="list-inline-item d-inline-block d-lg-none">
                                    <button className="navbar-toggler collapsed" type="button" data-bs-toggle="offcanvas" data-bs-target="#navbar-default" aria-controls="navbar-default" aria-label="Toggle navigation">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="currentColor" className="bi bi-text-indent-left text-primary" viewBox="0 0 16 16">
                                            <path
                                                d="M2 3.5a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5zm.646 2.146a.5.5 0 0 1 .708 0l2 2a.5.5 0 0 1 0 .708l-2 2a.5.5 0 0 1-.708-.708L4.293 8 2.646 6.354a.5.5 0 0 1 0-.708zM7 6.5a.5.5 0 0 1 .5-.5h6a.5.5 0 0 1 0 1h-6a.5.5 0 0 1-.5-.5zm0 3a.5.5 0 0 1 .5-.5h6a.5.5 0 0 1 0 1h-6a.5.5 0 0 1-.5-.5zm-5 3a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5z"
                                            />
                                        </svg>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </header>
        </>
    );
}

export default Header;
