import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import Dashboard from './Screens/Dashboard/Dashboard';
import ProductDetails from './Screens/ProductDetails/ProductDetails';
import Wishlist from './Screens/Wishlist/Wishlist';
import Cart from './Screens/Cart/Cart';
import ProductList from './Screens/ProductList/ProductList';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import AdminNavbar from './Screens/AdminNavbar/AdminNavbar';
import AdminDashbord from './Screens/AdminDashbord/AdminDashbord';
import AdminRegistrion from './Screens/AdminRegistrion/AdminRegistrion';
import AdminLogin from './Screens/AdminLogin/AdminLogin';
import AdminProducts from './Screens/AdminProducts/AdminProducts';
import AdminItems from './Screens/AdminItems/AdminItems';
import AdminCategory from './Screens/AdminCategory/AdminCategory';
import AdminSubcategory from './Screens/AdminSubcategory/AdminSubcategory';
import AdminSubSubCategory from './Screens/AdminSubSubCategory/AdminSubSubCategory';
<meta name="viewport" content="width=device-width, initial-scale=1" />

function App() {
    return (
        <Router> 
            <div>
                <Routes>
                    <Route path="/" element={<Navigate replace to="/dashboard" />} />
                    <Route path="/dashboard" element={<Dashboard />} />
                    <Route path="/product/:productId" element={<ProductDetails />} />
                    <Route path="/wishlist" element={<Wishlist />} />
                    <Route path="/cart" element={<Cart />} />
                    <Route path="/productList/:productListId" element={<ProductList />} />

                    {/*admin  */}
                   
                   
                    <Route path="/adminDashboard" element={<AdminDashbord />} />
                    <Route path="/AdminLogin" element={<AdminLogin />} />
                    <Route path="/products" element={<AdminProducts />} />
                    <Route path="/items" element={<AdminItems />} />
                    <Route path="/category" element={<AdminCategory />} />
                    <Route path="/subcategory" element={<AdminSubcategory />} />
                    <Route path="/sub-subcategory" element={<AdminSubSubCategory />} />
                </Routes>
                <ToastContainer />
            </div>
        </Router>
    );
}

export default App;
