import React from 'react'
import apple from '../../assets/images/products/product-img-1.jpg'
import Pineapple from '../../assets/images/products/product-img-2.jpg'
import kiwi from '../../assets/images/products/product-img-3.jpg'
import banana from '../../assets/images/products/product-img-4.jpg'
import Beatroot from '../../assets/images/products/product-img-5.jpg'
import Freshapple from '../../assets/images/products/product-img-6.jpg'
import GoldenPineapple from '../../assets/images/products/product-img-7.jpg'
import { BsHeart } from "react-icons/bs";
import { BsEye } from "react-icons/bs";
import { Link } from 'react-router-dom'

function PopularProducts() {
	const products = [
		{
			id: 1,
			name: "Haldiram's Sev Bhujia",
			image: apple,
			category: "Snack & Munchies",
			rating: 4.5,
			numReviews: 149,
			price: 18,
		},
		{
			id: 2,
			name: "NutriChoice Digestive",
			image: Pineapple,
			category: "Bakery & Biscuits",
			rating: 4.5,
			numReviews: 25,
			price: 24,
		},
		{
			id: 3,
			name: "Bakery & Biscuits",
			image: kiwi,
			category: "Cadbury 5 Star Chocolate",
			rating: 4.5,
			numReviews: 25,
			price: 24,
		},
		{
			id: 4,
			name: "Snack & Munchies",
			image: banana,
			category: "Onion Flavour Potato",
			rating: 4.5,
			numReviews: 25,
			price: 24,
		},
		{
			id: 5,
			name: "Instant Food",
			image: Beatroot,
			category: "Salted Instant Popcorn",
			rating: 4.5,
			numReviews: 25,
			price: 24,
		},
		{
			id: 6,
			name: "Dairy, Bread & Eggs",
			image: Freshapple,
			category: "Blueberry Greek Yogurt",
			rating: 4.5,
			numReviews: 25,
			price: 24,
		},
	];
	return (
		<>
			<section className="my-lg-14 my-8">
				<div className="container">
					<div className="row">
						<div className="col-12 mb-6">
							<h3 className="mb-0">Popular Products</h3>
						</div>
					</div>

					<div className="row g-4 row-cols-lg-5 row-cols-2 row-cols-md-3">
						{products.map((product) => (
							<Link to={`/product/${product.id}`} className="text-decoration-none text-inherit">
							<div key={product.id} className="col">
								<div className="card card-product">
									<div className="card-body">
										<div className="text-center position-relative">
											<a href="index.html#!">
												<img
													src={product.image}
													alt="Grocery Ecommerce Template"
													className="mb-3 img-fluid"
												/>
											</a>

											<div className="card-product-action">
												<a
													href="index.html#!"
													className="btn-action"
													data-bs-toggle="tooltip"
													data-bs-html="true"
													title="Quick View"
												>
													<BsEye />
												</a>
												<a
													href="index.html#!"
													className="btn-action"
													data-bs-toggle="tooltip"
													data-bs-html="true"
													title="Wishlist"
												>
													<BsHeart />
												</a>
											</div>
										</div>
										<div className="text-small mb-1">
											<a href="index.html#!" className="text-decoration-none text-muted">
												<small>{product.category}</small>
											</a>
										</div>
										<h2 className="fs-6">
											<a href="pages/shop-single.html" className="text-inherit text-decoration-none">
												{product.name}
											</a>
										</h2>
										<div>
											<small className="text-warning">
												{[...Array(Math.floor(product.rating))].map((_, index) => (
													<i key={index} className="bi bi-star-fill"></i>
												))}
												{product.rating % 1 !== 0 && (
													<i className="bi bi-star-half"></i>
												)}
											</small>
											<span className="text-muted small">{product.numReviews}</span>
										</div>
										<div className="d-flex justify-content-between align-items-center mt-3">
											<div>
												<span className="text-dark">${product.price}</span>
											</div>
											<div>
												<a href="index.html#!" className="btn btn-primary btn-sm">
													<svg
														xmlns="http://www.w3.org/2000/svg"
														width="16"
														height="16"
														viewBox="0 0 24 24"
														fill="none"
														stroke="currentColor"
														strokeWidth="2"
														strokeLinecap="round"
														strokeLinejoin="round"
														className="feather feather-plus"
													>
														<line x1="12" y1="5" x2="12" y2="19"></line>
														<line x1="5" y1="12" x2="19" y2="12"></line>
													</svg>
													Add
												</a>
											</div>
										</div>
									</div>
								</div>
							</div>
							</Link>
						))}
					</div>
				</div>
			</section>
		</>
	)
}

export default PopularProducts