import React, { useState } from 'react';
import signin from "../../assets/images/svg-graphics/signin-g.svg";
import { Link, useNavigate } from 'react-router-dom';
import useApi from '../../service/useApi';
import { toast } from 'react-toastify';

function AdminLogin({onLogin }) {
    const navigate = useNavigate();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [emailError, setEmailError] = useState('');
    const [passwordError, setPasswordError] = useState('');
    const { apiAction } = useApi();

    const validateEmail = (email) => {
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        return emailRegex.test(email);
    };

    // sign in
    const handleSignIn = async (e) => {
        e.preventDefault();
        setEmailError('');
        setPasswordError('');
        // Validate email
        if (!validateEmail(email)) {
            setEmailError('Please enter a valid email address.');
            return;
        }
        if (password.length < 6) {
            setPasswordError('The password field must be at least 8 characters.');
            return;
        }
        const requestBody = {
            email: email,
            password: password,
        };
        try {
            const response = await apiAction({
                url: "/admin/login",
                method: "POST",
                body: requestBody,
            });
            const adminLogin = response?.data;
            if (response.status === true && adminLogin) {
                localStorage.setItem('adminLogin', JSON.stringify(adminLogin));
                console.log(adminLogin, "----response");
                
                toast.success("Login successful!");
                // onLogin();

                setTimeout(() => {
                    navigate("/adminDashboard");
                }, 1000);
            } else {
               
            toast.error(response.errors[0]);
            }
        } catch (error) {
            toast.error('Login failed: ' + (error.message || 'Unknown error'));
        }
    };

    return (
        <>
            <main>
                {/* section */}
                <section className="my-lg-14 my-8">
                    <div className="container">
                        {/* row */}
                        <div className="row justify-content-center align-items-center">
                            <div className="col-12 col-md-6 col-lg-4 order-lg-1 order-2">
                                {/* img */}
                                <img
                                    src={signin}
                                    alt=""
                                    className="img-fluid"
                                />
                            </div>
                            {/* col */}
                            <div className="col-12 col-md-6 offset-lg-1 col-lg-4 order-lg-2 order-1">
                                <div className="mb-lg-9 mb-5">
                                    <h1 className="mb-1 h2 fw-bold">Sign in to FreshCart</h1>
                                    <p>Welcome back to FreshCart! Enter your email to get started.</p>
                                </div>
                                <form className="needs-validation" noValidate="" onSubmit={handleSignIn}>
                                    <div className="row g-3">
                                        {/* row */}
                                        <div className="col-12">
                                            {/* input */}
                                            <label
                                                htmlFor="formSigninEmail"
                                                className="form-label visually-hidden"
                                            >
                                                Email address
                                            </label>
                                            <input
                                                value={email}
                                                onChange={(e) => setEmail(e.target.value)}
                                                type="email"
                                                className={`form-control ${emailError ? 'is-invalid' : ''}`}
                                                id="formSigninEmail"
                                                placeholder="Email"
                                                required=""
                                            />
                                            <div className="invalid-feedback">
                                                {emailError}
                                            </div>
                                        </div>
                                        <div className="col-12">
                                            {/* input */}
                                            <div className="password-field position-relative">
                                                <label
                                                    htmlFor="formSigninPassword"
                                                    className="form-label visually-hidden"
                                                >
                                                    Password
                                                </label>
                                                <div className="password-field position-relative">
                                                    <input
                                                        value={password}
                                                        onChange={(e) => setPassword(e.target.value)}
                                                        type="password"
                                                        className={`form-control ${passwordError ? 'is-invalid' : ''}`}
                                                        id="formSigninPassword"
                                                        placeholder="*****"
                                                        required=""
                                                    />
                                                    <span>
                                                        <i className="bi bi-eye-slash passwordToggler" />
                                                    </span>
                                                    <div className="invalid-feedback">
                                                        {passwordError}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="d-flex justify-content-between">
                                            {/* form check */}
                                            <div className="form-check">
                                                <input
                                                    className="form-check-input"
                                                    type="checkbox"
                                                    defaultValue=""
                                                    id="flexCheckDefault"
                                                />
                                                {/* label */}
                                                <label
                                                    className="form-check-label"
                                                    htmlFor="flexCheckDefault"
                                                >
                                                    Remember me
                                                </label>
                                            </div>
                                            <div>
                                                Forgot password?
                                                <a href="forgot-password.html">Reset It</a>
                                            </div>
                                        </div>
                                        {/* btn */}
                                        <div className="col-12 d-grid">
                                            <button type="submit" className="btn btn-primary">
                                                Sign In
                                            </button>
                                        </div>
                                        {/* link */}
                                        {/* <div>
                                            Don’t have an account?
                                            <Link to="/signup">Sign Up</Link>
                                        </div> */}
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        </>
    );
}

export default AdminLogin;
