import React, { useEffect, useState } from 'react'
import AdminProfile from '../AdminProfile/AdminProfile'
import AdminNavbar from '../AdminNavbar/AdminNavbar'
import AdminCategoryModal from '../../commponets/AdminCategoryModal'
import useApi from '../../service/useApi';
import { toast } from 'react-toastify';
import { BsTrash, BsPencilSquare, BsThreeDotsVertical } from "react-icons/bs";

function AdminCategory() {
    const [modalShow, setModalShow] = useState(false);
    const [allPricebandList, setAllPricebandList] = useState([]);
    const [filteredPricebandList, setFilteredPricebandList] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);
    const { apiAction } = useApi();
    const adminLogin = JSON.parse(localStorage.getItem('adminLogin'));

  useEffect(() => {
    fetchPricebandList();
  }, [currentPage]); // Fetch list when currentPage changes

  useEffect(() => {
    filterPricebandList();
  }, [searchTerm, allPricebandList]); // Filter list when searchTerm or allPricebandList changes

  const fetchPricebandList = async () => {
    try {
      const response = await apiAction({
        url: `/admin/category/list?page=${currentPage}`,
        method: "GET",
        headers: {
          'Authorization': `Bearer ${adminLogin?.token}`
        }
      });
      if (response.status == true) {
        console.log(response,"-----response-");
        // toast.success(response?.message);
        const pricebandData = response?.data?.data;
        setAllPricebandList(pricebandData);
        localStorage.setItem('category', JSON.stringify(pricebandData));
        setFilteredPricebandList(response.data.data);
        setTotalPages(response.data.last_page); 
      } else {
        toast.error(response?.message);
      }
    } catch (error) {
      console.log(error, "------error");
    }
  };

  const filterPricebandList = () => {
    if (searchTerm.trim() === '') {
      setFilteredPricebandList(allPricebandList);
    } else {
      const filteredList = allPricebandList.filter(item =>
        item.type.toLowerCase().includes(searchTerm.toLowerCase())
      );
      setFilteredPricebandList(filteredList);
    }
  };

  const handleSearchInputChange = (e) => {
    setSearchTerm(e.target.value);
  };

  const handleModalSuccess = () => {
    fetchPricebandList(); // Refresh the list after adding a new type
  };

  const handleDeleteItem = async (itemId) => {
    try {
      const response = await apiAction({
        url: `/admin/category/delete/${itemId}`,
        method: "DELETE",
        headers: {
          'Authorization': `Bearer ${adminLogin?.token}`
        }
      });
      if (response.status) {
        console.log(response,"----responseresponse");
        toast.success(response?.message);
        // Update the list after successful deletion
        fetchPricebandList();
      } else {
        toast.error(response?.message);
      }
    } catch (error) {
      console.error("Error deleting item:", error);
      toast.error("Failed to delete item.");
    }
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };


    return (
        <div className="main-wrapper">
            <AdminProfile />
            <AdminNavbar />
            <AdminCategoryModal show={modalShow} onHide={() => setModalShow(false)} onSuccess={handleModalSuccess}  />
    
            <main className="main-content-wrapper">
                <div className="container">
                    {/* row */}
                    <div className="row mb-8">
                        <div className="col-md-12">
                            <div className="d-flex flex-column flex-md-row justify-content-between align-items-md-center gap-4">
                                <div>
                                    <h2>All Category</h2>
                                </div>
                                <div>
                                    <a href="#" role="button" className="btn btn-primary" onClick={() => setModalShow(true)}>
                                        Add New Item
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* row */}
                    <div className="row">
                        <div className="col-xl-12 col-12 mb-5">
                            {/* card */}
                            <div className="card h-100 card-lg">
                                <div className="p-6">
                                    <div className="row justify-content-between">
                                        <div className="col-md-4 col-12 mb-2 mb-md-0">
                                            {/* form */}
                                            <form className="d-flex" role="search">
                                            <input
                                            className="form-control"
                                            type="search"
                                            placeholder="Search Products"
                                            aria-label="Search"
                                            value={searchTerm}
                                            onChange={handleSearchInputChange}
                                        />
                                            </form>
                                        </div>
                                        <div className="col-lg-2 col-md-4 col-12">
                                            {/* select */}
                                            <select className="form-select">
                                                <option selected="">Status</option>
                                                <option value="Success">Success</option>
                                                <option value="Pending">Pending</option>
                                                <option value="Cancel">Cancel</option>
                                            </select>
                                        </div>
                                    </div>
                                </div>
                                {/* card body */}
                                <div className="card-body p-0">
                                    {/* table responsive */}
                                    <div className="table-responsive">
                                        <table className="table table-centered table-hover text-nowrap table-borderless mb-0 table-with-checkbox">
                                            <thead className="bg-light">
                                                <tr>
                                                    <th>
                                                        <div className="form-check">
                                                            {/* <input
                                                                className="form-check-input"
                                                                type="checkbox"
                                                                defaultValue=""
                                                                id="checkAll"
                                                            /> */}
                                                            <label
                                                                className="form-check-label"
                                                                htmlFor="checkAll"
                                                            />
                                                        </div>
                                                    </th>
                                                    <th>Image</th>
                                                    <th>Category Name</th>
                                                    <th>Priority</th>
                                                    <th>Date &amp; TIme</th>
                                                    <th></th>
                                                    <th></th>
                                                    <th></th>
                                                    <th />
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    allPricebandList.map((item)=>{
                                                        return(
                                                            <tr>
                                                            <td>
                                                                <div className="form-check">
                                                                    {/* <input
                                                                        className="form-check-input"
                                                                        type="checkbox"
                                                                        defaultValue=""
                                                                        id="orderOne"
                                                                    /> */}
                                                                    <label
                                                                        className="form-check-label"
                                                                        htmlFor="orderOne"
                                                                    />
                                                                </div>
                                                            </td>
                                                            <td>
                                                                <a href="order-list.html#!">
                                                                    <img
                                                                        src={item.image}
                                                                        alt=""
                                                                        className="icon-shape icon-md"
                                                                        style={{width:"50px" ,height:"50px"}}
                                                                    />
                                                                </a>
                                                            </td>
                                                          
                                                            <td>{item.name}</td>
                                                            <td>{item.priority}</td>
                                                            <td>{item.created_at}</td>
                                                           
                                                           
                                                            <td>
                                                                <div className="dropdown">
                                                                    <a
                                                                        href="order-list.html#"
                                                                        className="text-reset"
                                                                        data-bs-toggle="dropdown"
                                                                        aria-expanded="false"
                                                                    >
                                                                        <BsThreeDotsVertical />
                                                                        <i className="feather-icon icon-more-vertical fs-5" />
                                                                    </a>
                                                                    <ul className="dropdown-menu">
                                                                        <li>
                                                                            
                                                                            <button
                                                                            className=" btn-link  dropdown-item"
                                                                            onClick={() => handleDeleteItem(item.id)}>
                                                                            <a
                                                                                className="dropdown-item"
                                                                                href="#"
                                                                            >
                                                                                <BsTrash />
                                                                                <i className="bi bi-trash me-3" />
                                                                                Delete
                                                                            </a>
                                                                          
                                                                        </button>
                                                                        </li>
                                                                        <li>
                                                                        <button
                                                                            className=" btn-link  dropdown-item"
                                                                            onClick={() => handleDeleteItem(item.id)}>
                                                                            <a
                                                                                className="dropdown-item"
                                                                                href="order-list.html#"
                                                                            >
                                                                                  <BsPencilSquare />
                                                                                <i className="bi bi-pencil-square me-3" />
                                                                                Edit
                                                                            </a>
                                                                            </button>
                                                                        </li>
                                                                    </ul>
                                                                </div>
                                                            </td>
                                                        </tr> 
                                                        )
                                                    })
                                                }
                                                
                                             
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                                <div className="border-top d-flex justify-content-between align-items-md-center px-6 py-6 flex-md-row flex-column gap-4">
                    <span>Showing {currentPage} to {totalPages} of {allPricebandList.length} entries</span>
                    <nav>
                      <ul className="pagination mb-0">
                        <li className={`page-item ${currentPage === 1 ? 'disabled' : ''}`}>
                          <button className="page-link" onClick={() => handlePageChange(currentPage - 1)}>
                            Previous
                          </button>
                        </li>
                        {[...Array(totalPages)].map((_, index) => (
                          <li key={index} className={`page-item ${currentPage === index + 1 ? 'active' : ''}`}>
                            <button className="page-link" onClick={() => handlePageChange(index + 1)}>
                              {index + 1}
                            </button>
                          </li>
                        ))}
                        <li className={`page-item ${currentPage === totalPages ? 'disabled' : ''}`}>
                          <button className="page-link" onClick={() => handlePageChange(currentPage + 1)}>
                            Next
                          </button>
                        </li>
                      </ul>
                    </nav>
                  </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>

        </div>
    )
}

export default AdminCategory